import React,{Component} from 'react';

import { Grid, Cell, List, ListItem, ListItemContent,Button } from 'react-mdl';

class Contact extends Component {
    render(){
        return (
            <div className="contact-body">
                <Grid className="contact-grid">
                <Cell col={6}>
                    <h2>Sharath M</h2>
                    <img
                    src="https://cdn2.iconfinder.com/data/icons/avatar-2/512/Fred_man-512.png"
                    alt="avatar"
                    style={{height: '250px'}}
                    />
                    <p style={{ width: '75%', margin: 'auto', paddingTop: '1em'}}>Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book. It has survived not only five centuries</p>

                </Cell>
                <Cell col={6}>
                    <h2>Contact Me</h2>
                    <hr/>

                    <div className="contact-list">
                    <List>
                        <ListItem>
                        <ListItemContent style={{fontSize: '30px', fontFamily: 'Anton'}}>
                            <i className="fa fa-phone-square" aria-hidden="true"/>
                            <span class="contact-list-content">(91) 8123314294</span>
                        </ListItemContent>
                        </ListItem>

                        <ListItem>
                        <ListItemContent style={{fontSize: '30px', fontFamily: 'Anton'}}>
                            <i className="fa fa-envelope" aria-hidden="true"/>
                            <span class="contact-list-content">nayak.sharathm@gmail.com</span>
                        </ListItemContent>
                        </ListItem>

                    </List>
                    </div>

                    <h2>Get In Touch</h2>
                    <hr/>
                    <div className="contact-list">
                    <List>
                        <ListItem>
                        <ListItemContent style={{fontSize: '30px', fontFamily: 'Anton'}}>
                            <i className="fa fa-facebook-official" aria-hidden="true"/>
                            <a href="https://www.facebook.com/nayak.sharathm" rel="noopener noreferrer" target="_blank">Sharath Nayak</a>
                        </ListItemContent>
                        </ListItem>

                        <ListItem>
                        <ListItemContent style={{fontSize: '30px', fontFamily: 'Anton'}}>
                            <i className="fa fa-twitter" aria-hidden="true"/>
                            <a href="https://twitter.com/sharu" rel="noopener noreferrer" target="_blank">@sharu</a>
                        </ListItemContent>
                        </ListItem>

                    </List>
                    </div>


                </Cell>
                </Grid>
            </div>
        )
    }
}

export default Contact;


